  .screen-mask {
      z-index: 1;
      width: 100%;
      transition: z-index 0.1s linear;
      perspective: 1200px;
      perspective-origin: 80% 80%;
      margin-left: 50px;
      display: inline-block;
      position: relative;
  
      &__image-wrapper {
        transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
        transform: rotateX(2deg) rotateY(-14deg) rotate(1deg) translateX(-10%);
        box-shadow: 10px 20px 80px rgba(0,0,0,0.2);
        width: 600px;
        border-radius: 6px;
        @media only screen and (max-width: 1200px) {
          width: 360px;
        }
        @media only screen and (max-width: 900px) {
          transition: none;
          transform: none;
        }
        &--block {
          height: 348px;
          @media only screen and (max-width: 1200px) {
            height: 208px;
          }
          a.view-project {
            position: absolute;
            right: 0px;
            top: -20px;
            color: #231f20;
            display: inline-block;
            font-weight: bold;
            font-size: 16px;
            font-family: Helvetica Neue,"nimbus-sans",Helvetica,Arial,sans-serif;
            line-height: 1;
            text-decoration: none;
            transition: color .3s;
            padding-left: 22px;
            @media only screen and (max-width: 1200px) {
              font-size: 10px;
            }
            @media only screen and (max-width: 900px) {
              right: 20%;
            }
          }
          a.view-project:before {
              background-image: url("../assets/images/external_link_grey_dk.svg");
              background-size: cover;
              background-repeat: no-repeat;
              content: '';
              display: block;
              height: 22.624px;
              left: 0;
              position: absolute;
              top: 0;
              transition: background .3s;
              width: 16px;
          }
          a.view-project:after {
              background: #231f20;
              content: '';
              left: 0;
              height: 2.828px;
              position: absolute;
              top: -2.828px;
              transition: background .3s;
              width: 100%;
          }
          a.view-project:hover:before {
              background-image: url("../assets/images/external_link_yellow.svg");
          }
          a.view-project:hover:after {
              background: #f1c40f;
          }
        }
        img {
          width: 100%;
          border-radius: 6px;
          @media only screen and (max-width: 900px) {
          }
        }
      }
  }
  .main-block {
    width: 630px;
    height: auto;
    background: #ffffff;
    position: relative;
    top: 70px;
    left: 100px;
    box-shadow: 0 0 0 1px rgba(0,0,0,0.10), 0 2px 4px 0 rgba(0,0,0,0.10);
    border-radius: 7px;
    font-family: 'Georgia';
    font-size: 23px;
    line-height: 33px;
    // min-width: 360px;
    @media only screen and (max-width: 1270px) {
      width: 50%;
      left: 5%;
    }
    @media only screen and (max-width: 900px) {
      width: 90%;
      // top: 200px;
    }
  
    &__frameworks-container {
      display: inline-block;
      font-family: 'HelveticaNeue';
      background: #333333;
      margin-bottom: 5px;
      color: white;
      font-weight: bold;
      border-radius: 4px;
      @media only screen and (max-width: 1270px) {
        padding: 2px;
      }
      @media only screen and (max-width: 425px) {
        line-height: 20px;
      }
      div {
        display: inline-block;
        font-size: 14px;
        vertical-align: middle;
        padding: 5px;
        @media only screen and (max-width: 425px) {
          font-size: 12px;
        }
      }
    }
  
    span {
      margin-bottom: 30px;
      margin-top: 0;
      display: inline;
      padding: 5px 0;
      line-height: 1.7;
      @media only screen and (max-width: 1270px) {
        font-size: 20px;
      }
      @media only screen and (max-width: 425px) {
        font-size: 14px;
        margin-bottom: 15px;
        line-height: 1.9;
      }
    }
    &__inner-wrapper {
      margin-left: 50px;
      margin-right: 130px;
      margin-bottom: 430px;
      @media only screen and (max-width: 1270px) {
        margin-right: 100px;
      }
      @media only screen and (max-width: 900px) {
        margin-right: 50px;
      }
    }
    &__icon-wrapper {
        position: absolute;
      .speech-bubble {
        position: relative;
        background: #027E79;
        color: #fff;
        top: 60px;
        left: 94px;
        padding: 7px;
        width: 86px;
        font-size: 16px;
        display: none;
        font-family: 'HelveticaNeue';
        transition:0.3s ease all;
        box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 4px 0 rgba(0, 0, 0, 0.1);
        &:before {
          content: '';
          border-style: solid;
          border-width: 12px 15px 1px 0;
          border-color: transparent #027E79 transparent transparent;
          position: absolute;
          left: -15px;
        }
      }
      &__profile-image {
        position: absolute;
        margin-top: 50px;
        overflow: hidden;
        height: 70px;
        width: 70px;
        img {
          height: 70px;
          width: 70px;
        }
      }
    }
    &__intro {
      &--wrapper {
        padding-top: 160px
      }
    }
    &__left-half {
      display: inline-block;
    }
    &__right-half {
      display: inline-block;
    }
    &__fca, &__salesforce {
      &--header {
        margin-top: 50px;
      }
    }
    &__intro, &__salesforce, &__movie-vue, &__contact, &__remote-dir, &__fca, &__marketing, &__daily-nclex {
      margin-bottom: 30px;
      color: #333333;
      @media only screen and (max-width: 425px) {
        margin-bottom: 20px;
      }
        &--header{
          span {
            color: #333333 !important;
            font-weight: bold;
            font-size: 34px !important;
            margin-top: 20px;
            margin-bottom: 28px;
            font-family: 'HelveticaNeue-Bold';
  
            @media only screen and (max-width: 1270px) {
              font-size: 24px !important;
            }
            @media only screen and (max-width: 425px) {
              font-size: 18px !important;
            }
          }
        }
        &--header span a {
          color: #333333 !important;
          font-weight: bold;
          font-size: 24px !important;
          margin-top: 20px;
          margin-bottom: 28px;
          font-family: 'HelveticaNeue-Bold';
          text-decoration: none;
          @media only screen and (max-width: 425px) {
            font-size: 18px !important;
          }
          &::after {
            background-image: url("../assets/images/external_link_grey_dk.svg");
            background-size: cover;
            background-repeat: no-repeat;
            content: '';
            display: inline-block;
            height: 24px;
            position: relative;
            transition: background .3s;
            width: 24px;
            right: 0;
            margin-left: 5px;
            top: 3px;
          }
          &::after:hover {
            background-image: url("../assets/images/external_link_yellow.svg") !important;
          }
        }
      }
      &__contact {
        &--end {
          padding-bottom: 50px;
        }
      }
      &__remote-dir {
        &--list-item {
          display: block !important;
          margin-bottom: 5px !important;
          font-size: 14px;
          margin-top: 5px !important;
          line-height: 1 !important;
          &:nth-child(2) {
            margin-top: 30px !important;
          }
        }
      }
  }
